// components/PlyViewer.tsx
import React, { Suspense, useEffect, useMemo } from 'react';
import { withPrefix } from 'gatsby';
import { Canvas, useLoader } from '@react-three/fiber';
import { PLYLoader } from 'three/examples/jsm/loaders/PLYLoader.js';
import { OrbitControls, PerspectiveCamera, Bounds } from '@react-three/drei';
import { ConvexGeometry } from 'three/examples/jsm/geometries/ConvexGeometry';
import { Delaunay } from 'd3-delaunay';
import * as THREE from 'three';

// Fallback while loading
const LoadingFallback: React.FC<{ color?: string }> = ({ color = 'grey' }) => (
  <mesh>
    <boxGeometry args={[1, 1, 1]} />
    <meshStandardMaterial color={color} />
  </mesh>
);

// ---------- URL helpers (robust to plain, absolute, already-prefixed, or external) ----------
const isExternal = (p: string) => /^https?:\/\//i.test(p);
const toDotPath = (p: string) => (p.startsWith('./') ? p : p.startsWith('/') ? `.${p}` : `./${p}`);
// __PATH_PREFIX__ is injected by Gatsby at build time when using --prefix-paths
declare const __PATH_PREFIX__: string | undefined;
const isAlreadyPrefixed = (p: string) => {
  try {
    const pp = typeof __PATH_PREFIX__ === 'undefined' ? '' : __PATH_PREFIX__;
    return !!pp && (p.startsWith(`${pp}/`) || p.startsWith(`./${pp}/`) || p.startsWith(`/${pp}/`));
  } catch {
    return false;
  }
};
const resolveUrl = (p: string) => {
  if (isExternal(p) || isAlreadyPrefixed(p)) return p;
  return withPrefix(toDotPath(p)); // mirrors your <source src={withPrefix("./…")} />
};
// -------------------------------------------------------------------------------------------

// Render one PLY as points, convex-hull mesh, or heightmap surface
interface PlyObjectProps {
  url: string;
  color: string;
  useVertexColors: boolean;
  pointSize: number;
  renderMesh?: boolean;
  renderSurface?: boolean;
  center?: boolean;
}

const PlyObject: React.FC<PlyObjectProps> = ({
  url,
  color,
  useVertexColors,
  pointSize,
  renderMesh = false,
  renderSurface = false,
  center = true,
}) => {
  // Resolve to a final URL that works on GitHub Pages with pathPrefix
  const finalUrl = useMemo(() => resolveUrl(url), [url]);

  // Load geometry
  const geometry = useLoader(PLYLoader, finalUrl);
  const hasColorAttr = !!geometry.attributes.color;

  // Decide which geometry to display
  const displayGeometry = useMemo<THREE.BufferGeometry>(() => {
    if (renderSurface) {
      // Triangulate on XY plane; Z is height
      const positions = geometry.attributes.position.array as Float32Array;
      const pts2d: [number, number][] = [];
      for (let i = 0; i < positions.length; i += 3) {
        pts2d.push([positions[i], positions[i + 1]]);
      }
      const delaunay = Delaunay.from(pts2d);
      const indices = new Uint32Array(delaunay.triangles); // ensure typed array
      const surfGeom = new THREE.BufferGeometry();
      surfGeom.setAttribute('position', geometry.attributes.position);
      if (hasColorAttr) surfGeom.setAttribute('color', geometry.attributes.color);
      surfGeom.setIndex(new THREE.BufferAttribute(indices, 1));
      return surfGeom;
    }
    if (renderMesh) {
      if (geometry.index) return geometry;
      const posArr = geometry.attributes.position.array as Float32Array;
      const pts: THREE.Vector3[] = [];
      for (let i = 0; i < posArr.length; i += 3) {
        pts.push(new THREE.Vector3(posArr[i], posArr[i + 1], posArr[i + 2]));
      }
      return new ConvexGeometry(pts);
    }
    return geometry; // raw points
  }, [geometry, renderMesh, renderSurface, hasColorAttr]);

  useEffect(() => {
    if (center) {
      geometry.computeBoundingBox();
      geometry.center();
      if (displayGeometry !== geometry) {
        displayGeometry.computeBoundingBox();
        displayGeometry.center();
      }
    }
    if (renderMesh || renderSurface) {
      displayGeometry.computeVertexNormals();
    }
  }, [geometry, displayGeometry, center, renderMesh, renderSurface]);

  // Mesh/surface
  if (renderMesh || renderSurface) {
    return (
      <mesh geometry={displayGeometry}>
        <meshStandardMaterial
          vertexColors={useVertexColors && hasColorAttr}
          color={color}
          side={THREE.DoubleSide}
          flatShading={!hasColorAttr}
          transparent
          opacity={0.6}
          depthWrite={false}
        />
      </mesh>
    );
  }

  // Points
  return (
    <points geometry={geometry}>
      <pointsMaterial
        size={pointSize}
        sizeAttenuation
        vertexColors={useVertexColors && hasColorAttr}
        color={color}
      />
    </points>
  );
};

interface PlyViewerProps {
  urls: string[];
  pointColors: string[];
  useVertexColors?: boolean;
  pointSize?: number;
  renderMesh?: boolean;
  renderSurface?: boolean;
  width?: string;
  height?: string;
  center?: boolean;
}

const PlyViewer: React.FC<PlyViewerProps> = ({
  urls,
  pointColors,
  useVertexColors = true,
  pointSize = 0.0015,
  renderMesh = false,
  renderSurface = false,
  width = '100%',
  height = '400px',
  center = true,
}) => {
  if (!urls.length || urls.length !== pointColors.length) return null;

  return (
    <div style={{ width, height }}>
      <Canvas className="border-2 border-slate-100 rounded-xl">
        <PerspectiveCamera makeDefault position={[0, 0, 5]} />
        <ambientLight intensity={0.7} />
        <pointLight position={[10, 10, 10]} intensity={1} />

        <Suspense fallback={<LoadingFallback />}>
          <Bounds fit clip margin={0.1}>
            {urls.map((url, i) => (
              <PlyObject
                key={`${url}-${i}`}
                url={url} // can be "./models/…", "/models/…", "/granular_rl/models/…", or full https URL
                color={pointColors[i]}
                useVertexColors={useVertexColors}
                pointSize={pointSize}
                renderMesh={renderMesh}
                renderSurface={renderSurface}
                center={center}
              />
            ))}
          </Bounds>
        </Suspense>

        <OrbitControls enablePan enableZoom enableRotate autoRotate />
      </Canvas>
    </div>
  );
};

export default PlyViewer;
